@import "../../styles/utils/mixins";

.containerError {
  @include centerAll;
  flex-direction: column;
  margin-top: 129px;

  @media screen and (max-width: 992px) {
    margin-top: 129px;
  }
  @media screen and (max-width: 768px) {
    margin-top: 175px;
  }

  img {
    margin: 30px 0;

    @media screen and (max-width: 992px) {
      margin: 26px 0;
      line-height: 25.67px;
    }
    @media screen and (max-width: 768px) {
      width: 200px;
      height: auto;
      margin: 10px 0;
      line-height: 25.67px;
    }
  }

  .errorTitle {
    font-size: 36px;
    font-weight: 500;
    line-height: 51px;
    letter-spacing: 0em;
    margin-top: 66px;
    margin-bottom: 182px;
    text-align: center;

    @media screen and (max-width: 992px) {
      margin-top: 11px;
      font-size: 26px;
      margin-bottom: 133px;
      line-height: 24px;
    }
    @media screen and (max-width: 768px) {
      font-size: 18px;
      margin-bottom: 133px;
    }
  }

  .backToHome {
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    letter-spacing: 0em;
    margin-bottom: 159px;

    @media screen and (max-width: 992px) {
      font-size: 16px;
    }
    @media screen and (max-width: 768px) {
      font-size: 14px;
      margin-bottom: 235px;
    }
  }
}
