@mixin centerAll {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin centerY {
  display: flex;
  align-items: center;
}

@mixin centerX {
  display: flex;
  justify-content: center;
}

@mixin marginContainer {
  margin-left: 6.94%;
  margin-right: 6.94%;

  @media screen and (max-width: 992px) {
    margin-left: 5.33%;
    margin-right: 5.33%;
  }
}

@mixin fullSize {
  width: 100%;
  height: 100%;
}
